



































































import mInput from '../../components/m-input.vue';
export default {
	components: {
		mInput
	},
	//加载金额
	onLoad() {
		const agentInfo = uni.getStorageSync('agentInfo');
		if (agentInfo) {
			this.token=agentInfo.token;
		}
		this.getMoney()
	},
	data() {
		return {
			ztsmoney:'0.00',
			thismoney:'0.00',
			ktmoney:'0.00',
			minmoney:'100',
			maxmoney:'1000',
			text1:"",
			text2:"",
			text3:"",
			
			id:0,
			money:'',
			password:'',
			
			modalName: null,
			gridCol: 3,
			gridBorder: true,
			
			menuBorder: true,
			menuArrow: true,
			menuCard: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null,
			token:'',
		};
	},
	methods: {
		navTo() {
			uni.redirectTo({
				url: '../tabbar/tabbar'
			});
		},
		initPosition() {
		    this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		// 点击提现
		ontsmoney(){
			uni.request({
				url: 'http://192.168.0.199:8080/agent/earnings/ajax-apply-deposit',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						token:this.token,
						money:this.money,
						password:this.password,
					},
					success: res => {
						let list=res;
						if(list.data.code==200){
							let data=list.data;
							console.log(data);
							uni.showToast({
								icon: 'none',
								title: '提现成功'
							});
							
							this.getMoney()
						}else{
							let data=list.data;
							console.log(data);
							uni.showToast({
								icon: 'none',
								title: data.msg
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
		},
		getMoney(){
			uni.request({
				url: 'http://192.168.0.199:8080/agent/earnings/ajax-burse-money',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						token:this.token,
					},
					success: res => {
						if(res.data.code==200){
							let data=res.data.data;
							this.ztsmoney=data.alltsMoney;
							this.thismoney=data.thisMoney;
							this.ktmoney=data.ktMoney;
							let configval=JSON.parse(data.configval);
								if(configval[0].status==0){
									let minPrice=configval[0].minPrice
									this.text1="每次提现范围:"+configval[0].minPrice+"<金额<"+configval[0].maxPrice+"，最多可提现"+configval[0].num+"次，2~24小时到账";
								}else if(configval[1].status==0){
									let minPrice=configval[1].minPrice
									this.text2="每次提现范围:"+configval[1].minPrice+"<金额<"+configval[1].maxPrice+"，最多可提现"+configval[1].num+"次，每周五24:00前提交的提现申请将在下周一前17:00结算";
								}else if(configval[2].status==0){
									let minPrice=configval[2].minPrice
									this.text3="每次提现范围:"+configval[2].minPrice+"<金额<"+configval[2].maxPrice+"，最多可提现"+configval[2].num+"次，每月最后一天24:00前提交的提现申请将在下月第一个工作日17:00结算";
								}
						}else{
							console.log('请设置提现密码再提现')
						}
					},
					fail: () => {},
					complete: () => {}
				});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},

		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	},
	onReady() {
	    this.initPosition();
	}
}
